import { useState } from 'react';
import {db} from '../Firebase/firebase';
import { doc, getDoc, setDoc } from 'firebase/firestore';
import classes from './URLShortener.module.css';
import Card from '../UI/Card';

const URLShortener = () => {
    const [url, setUrl] = useState("");
    const [shortCode, setShortCode] = useState("");
    const [postShorten, setPostShorten] = useState();

    const formSubmitHandler = async (event) => {
        event.preventDefault();
        if (url.length === 0) {
            return alert("URL cannot be empty");
        }
        let tempShortCode;
        if (shortCode.length === 0) {
            tempShortCode = generateShortCode();
        }
        const docSnap = await getDoc(doc(db, "urls", shortCode.length === 0 ? tempShortCode : shortCode));
        if (docSnap.exists()) {
            console.log("Already exists!");
        } else {
            const newDocRef = doc(db, "urls", shortCode.length === 0 ? tempShortCode : shortCode);
            await setDoc(newDocRef, {url});
            setPostShorten(<div>
                <div>Done! https://gagantk.me/#/s/{shortCode}</div>
                <button className={classes.copy} onClick={() => navigator.clipboard.writeText(`https://gagantk.me/#/s/${shortCode}`)}>Copy</button>
                </div>)
        }
    }

    const generateShortCode = () => {
        setShortCode(Math.random().toString(36).substring(2, 7));
        return shortCode;
    }

    return (
        <div className={classes.AppHeader}>
            <Card>
        <div className={classes.H1}>
            <h1>URL Shortener</h1>
        </div>
        <form onSubmit={formSubmitHandler} className={classes.Form}>
            <div className={classes.url}>
                <input type="text" minLength='1' value={url} onChange={(element) => setUrl(element.target.value.trim())} placeholder='https://example.com' />
                <button>Shorten!</button>
            </div>
            <div className={classes.custom}>
                https://gagantk.me/#/s/<input type="text" value={shortCode} onChange={(element) => setShortCode(element.target.value.trim())} placeholder='Enter custom URL' />
            </div>
        </form>
        <div className={classes.post}>{postShorten}</div>
        </Card>
        </div>
    )
}

export default URLShortener;